<template>
    <div class="my-address">
        <div class="address-list">
            <cube-scroll>
            <cube-swipe>
                <transition-group class="list-wrapper" name="swipe" tag="ul">
                    <li v-for="(item, index) in data" class="list-item-wrap" :key="index">
                        <cube-swipe-item
                        ref="swipeItem"
                        :btns="item.btns"
                        :index="index"
                        class="list-item"
                        @btn-click="onBtnClick">
                        <div class="info">
                            <div class="name">{{item.item.receiverName}}</div>
                            <div class="mobile">{{item.item.phone}}</div>
                        </div>
                        <div class="desc">
                            <div class="address">
                                <span class="default" v-if="item.item.hasDefault === 1">默认</span>
                                {{item.item.province}}
                                {{item.item.city}}
                                {{item.item.area}}
                                {{item.item.address}}
                            </div>
                            <div class="edit" @click="_edit(item.item)">编辑</div>
                        </div>
                        </cube-swipe-item>
                    </li>
                </transition-group>
            </cube-swipe>
            </cube-scroll>
        </div>
        <div class="address-add">
            <div class="btn" @click="_add()">新增地址</div>
        </div>
    </div>
</template>
<script>
const swipeData = [
        {
          item: {
            id: '3646653877',
            name: '还不是因为你长得不好看',
            desc: '伤感：歌词再狠，也抵不过现实伤人',
            imgurl: 'http://p.qpic.cn/music_cover/MhQ4bJBPt3Yt5icXyBGNhyPJnE9O51CqaN72iaDgvFmDKaia12UFhU5uQ/600?n=1'
          },
          btns: [
            {
              action: 'clear',
              text: '设为默认',
              color: '#c8c7cd'
            },
            {
              action: 'delete',
              text: '删除',
              color: '#ff3a32'
            }
          ]
        },
        {
          item: {
            id: '1789676645',
            name: '秋水浮萍任飘渺',
            desc: '『武侠配乐』快意恩仇江湖情',
            imgurl: 'http://p.qpic.cn/music_cover/8KfvDey9cibtZ5xkWxRic6vhXgdPic3wnB7reibI4pdPQBCP8u57uqcjsQ/600?n=1'
          },
          btns: [
              {
              action: 'clear',
              text: '设为默认',
              color: '#c8c7cd'
            },
            {
              action: 'delete',
              text: '删除',
              color: '#ff3a32'
            }
          ]
        },
]
import { queryDefaultReceiveArea, queryReceiveArea, updateReceiverAddress, deleteReceiverAddress } from '@/api/address.js';
export default {
    name: 'my-address',
    data() {
        return {
            data: [],
        }
    },
    created() {
        this._queryReceiveArea();
    },
    methods: {
        // 所有地址
        _queryReceiveArea() {
            let params = {
                userId: 1,
            }
            queryReceiveArea(params).then(res => {
                res.forEach(item => {
                    this.data.push({
                        item: item,
                        btns: [
                            {
                                action: 'clear',
                                text: '设为默认',
                                color: '#c8c7cd'
                            },
                            {
                                action: 'delete',
                                text: '删除',
                                color: '#ff3a32'
                            }
                        ]
                    })
                })
            })
        },
        onBtnClick(btn, index) {
            if (btn.action === 'delete') {
                this.$createActionSheet({
                title: '确认要删除吗',
                active: 0,
                data: [
                    {content: '删除'}
                ],
                onSelect: () => {
                    this._onDel(this.data[index].item);
                }
                }).show()
            } else {
                this.$refs.swipeItem[index].shrink()
            }
            if (btn.action === 'clear') {
                this.$createActionSheet({
                title: '确认要设置默认吗',
                active: 0,
                data: [
                    {content: '设置'}
                ],
                onSelect: () => {
                    this._onSelect(this.data[index].item);
                }
                }).show()
            } else {
                this.$refs.swipeItem[index].shrink()
            }
        },
        // 设置默认
        _onSelect(obj) {
            let params = obj;
            delete params.gmtCreate;
            delete params.gmtModify;
            params.hasDefault = 1;
            updateReceiverAddress(params).then(res => {
                location.reload();
            })
        },
        // 删除
        _onDel(obj) {
            if (obj.hasDefault === 1) {
                this.toast = this.$createToast({
                    txt: '无法删除默认地址',
                    type: 'txt'
                })
                this.toast.show()
                return;
            }
            let params = {
                id: obj.id
            }
            deleteReceiverAddress(params).then(res => {
                this.toast = this.$createToast({
                    txt: '操作成功',
                    type: 'txt',
                    time: 500,
                    onTimeout: () => {
                        location.reload();                                           
                    }
                })
                this.toast.show()
            })
        },
        // 编辑
        _edit(item) {
            this.$router.push({ path: '/address-edit', query: {id: item.id}});
        },
        // 新增
        _add() {
            this.$router.push({ path: '/address-edit'});
        },
    }
}
</script>
<style lang="stylus" scoped>
.my-address {
    height: 100vh;
    font-family: HiraginoSansGB-W3;
    background-color: #FFFFFF;
    .address-list {
        height: calc(100vh - 120px);
        .list-wrapper {
            .list-item-wrap {
                .list-item {
                    padding: 24px;
                    border-bottom: 0.5px solid #F2F2F2;
                    .info {
                        display: flex;
                        margin-bottom: 12px;
                        height: 32px;
                        line-height: 32px;
                        .name {
                            margin-right: 24px;
                        }
                        .mobile {
                            font-size: 24px;
                            color: #909497;
                        }
                    }
                    .desc {
                        display: flex;
                        margin-top: 24px;
                        justify-content: space-between;
                        .address {
                            font-size: 24px;
                            .default {
                                padding: 2px 5px;
                                background-color : #FAD7A0;
                                color: #E74C3C;
                                border-radius: 5px;
                            }
                        }
                    }
                }
            }
        }
    }
    .address-add {
        height: 120px;
        .btn {
            margin: 10px auto;
            width: 600px;
            height: 98px;
            line-height: 98px;
            color: #ffffff;
            text-align: center;
            background:rgba(74,76,89,1);
            border-radius:10px;
        }
    }
}
</style>


